import Vuex from 'vuex'
import Vue from 'vue'

//vuex
Vue.use(Vuex);

const state = {
  flag: true,
  loginStatus: JSON.parse(sessionStorage.getItem("userName")),
  data: [
    { name: "", children: [{ name: "", path: "/hu" }, { name: "", path: "/zai" }] },
    { name: "", children: [], path: "/lonely" },
    { name: "", children: [{ name: "", path: "/hua" }, { name: "", path: "/huaa" }] },
  ]
}

const mutations = {
  changeFlag(state, payload) {
    state.flag = payload.flag;
  },
  changeLoginStatus(state, payload) {
    sessionStorage.setItem('userName', JSON.stringify(payload));
    state.loginStatus = payload;
  }
}
export default new Vuex.Store({
  state,
  mutations
})


